/* App.css */
.todo-app {
  text-align: center;
  padding: 20px;
}

.todo-list {
  display: flex;
  flex-direction: column;
}

.todo-card {
  background-color: #f2f2f2;
  margin: 5px 0;
  padding: 10px;
  cursor: pointer;
}

.completed {
  text-decoration: line-through;
  background-color: #d1ffd6;
}

.reset-button {
  margin-top: 10px;
  background-color: #ff5252;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}
